.cart {
  max-width: 520px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 100px;
}
.cart img {
  width: 112px;
  height: 112px;
  margin-right: 50px;
  margin-top: 5px;
}
.subtitle {
  margin-bottom: 15px;
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 24px;
  font-style: normal;
  color: #292929;
}
.text {
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  color: #292929;
}

@media screen and (max-width: 1100px) {
  .cart {
    margin: 0 auto 60px;
  }
}
@media screen and (max-width: 576px) {
  .cart {
    flex-direction: column;
    text-align: center;
    width: 320px;
  }
  .cart img {
    margin: 0 auto 20px;
  }
}
